"use client"

import { useState, useEffect } from "react"
import { User, Mail, LogOut } from "lucide-react"
import { useRouter } from "next/navigation"

interface UserData {
  id: string
  email: string
  name: string
  createdAt: string
  bio?: string
  timezone?: string
  workStartTime?: string
  workEndTime?: string
}

export default function ProfilePage() {
  const router = useRouter()
  const [user, setUser] = useState<UserData | null>(null)
  const [isEditing, setIsEditing] = useState(false)
  const [bio, setBio] = useState("")
  const [timezone, setTimezone] = useState("UTC")
  const [workStartTime, setWorkStartTime] = useState("09:00")
  const [workEndTime, setWorkEndTime] = useState("17:00")

  useEffect(() => {
    const userData = localStorage.getItem("currentUser")
    if (userData) {
      const parsed = JSON.parse(userData)
      setUser(parsed)
      setBio(parsed.bio || "")
      setTimezone(parsed.timezone || "UTC")
      setWorkStartTime(parsed.workStartTime || "09:00")
      setWorkEndTime(parsed.workEndTime || "17:00")
    }
  }, [])

  const handleSaveProfile = () => {
    if (user) {
      const updated = {
        ...user,
        bio,
        timezone,
        workStartTime,
        workEndTime,
      }
      localStorage.setItem("currentUser", JSON.stringify(updated))
      setUser(updated)
      setIsEditing(false)
    }
  }

  const handleLogout = () => {
    localStorage.removeItem("currentUser")
    router.push("/")
  }

  if (!user) return <div>Loading...</div>

  return (
    <div className="p-4 lg:p-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Profile</h1>
        <p className="text-muted-foreground">Manage your account and preferences</p>
      </div>

      <div className="max-w-2xl space-y-6">
        {/* Profile Card */}
        <div className="bg-card rounded-xl shadow-md p-8 border border-border">
          <div className="flex items-center gap-6 mb-8">
            <div className="w-20 h-20 bg-primary rounded-full flex items-center justify-center">
              <User className="w-10 h-10 text-primary-foreground" />
            </div>
            <div>
              <h2 className="text-2xl font-bold text-foreground">{user.name}</h2>
              <div className="flex items-center gap-2 text-muted-foreground mt-1">
                <Mail size={16} />
                {user.email}
              </div>
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4 pb-6 border-b border-border">
            <div>
              <p className="text-sm text-muted-foreground mb-1">Account Created</p>
              <p className="font-medium text-foreground">{new Date(user.createdAt).toLocaleDateString()}</p>
            </div>
            <div>
              <p className="text-sm text-muted-foreground mb-1">Member ID</p>
              <p className="font-medium text-foreground">{user.id}</p>
            </div>
          </div>
        </div>

        {/* Settings Card */}
        <div className="bg-card rounded-xl shadow-md p-8 border border-border">
          <div className="flex items-center justify-between mb-6">
            <h3 className="text-xl font-bold text-foreground">Preferences</h3>
            <button
              onClick={() => setIsEditing(!isEditing)}
              className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                isEditing
                  ? "bg-destructive text-destructive-foreground"
                  : "bg-primary text-primary-foreground hover:bg-primary/90"
              }`}
            >
              {isEditing ? "Cancel" : "Edit"}
            </button>
          </div>

          <div className="space-y-4">
            {/* Bio */}
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Bio</label>
              {isEditing ? (
                <textarea
                  value={bio}
                  onChange={(e) => setBio(e.target.value)}
                  placeholder="Tell us about yourself"
                  rows={3}
                  className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                />
              ) : (
                <p className="text-foreground/80">{bio || "No bio added yet"}</p>
              )}
            </div>

            {/* Timezone */}
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Timezone</label>
              {isEditing ? (
                <select
                  value={timezone}
                  onChange={(e) => setTimezone(e.target.value)}
                  className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                >
                  <option>UTC</option>
                  <option>EST</option>
                  <option>CST</option>
                  <option>MST</option>
                  <option>PST</option>
                </select>
              ) : (
                <p className="text-foreground/80">{timezone}</p>
              )}
            </div>

            {/* Work Hours */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Work Start Time</label>
                {isEditing ? (
                  <input
                    type="time"
                    value={workStartTime}
                    onChange={(e) => setWorkStartTime(e.target.value)}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  />
                ) : (
                  <p className="text-foreground/80">{workStartTime}</p>
                )}
              </div>
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Work End Time</label>
                {isEditing ? (
                  <input
                    type="time"
                    value={workEndTime}
                    onChange={(e) => setWorkEndTime(e.target.value)}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                  />
                ) : (
                  <p className="text-foreground/80">{workEndTime}</p>
                )}
              </div>
            </div>

            {/* Save Button */}
            {isEditing && (
              <button
                onClick={handleSaveProfile}
                className="w-full mt-6 bg-primary hover:bg-primary/90 text-primary-foreground px-4 py-2 rounded-lg font-medium transition-colors"
              >
                Save Changes
              </button>
            )}
          </div>
        </div>

        {/* Danger Zone */}
        <div className="bg-destructive/10 rounded-xl shadow-md p-8 border border-destructive/20">
          <h3 className="text-xl font-bold text-destructive mb-4">Danger Zone</h3>
          <p className="text-destructive/80 mb-4">Logging out will end your current session.</p>
          <button
            onClick={handleLogout}
            className="flex items-center gap-2 bg-destructive hover:bg-destructive/90 text-destructive-foreground px-6 py-2 rounded-lg font-medium transition-colors"
          >
            <LogOut size={20} />
            Logout
          </button>
        </div>
      </div>
    </div>
  )
}
